<template>
	<div>
    <van-nav-bar
      title="密码修改"
      left-text="返回"
      right-text=""
      left-arrow
      @click-left="goBack"
    />
		<van-cell-group>
			<van-field
				label="原密码"
				v-model="password"
				type="password"
				placeholder="请输入原密码"
				:error="!!$vuelidation.error('password')"/>

			<van-field
				label="新密码"
				v-model="new_password"
				type="password"
				placeholder="请输入新密码"
				:error="!!$vuelidation.error('new_password')" />

			<van-field
				label="确认密码"
				v-model="repeat_password"
				type="password"
				placeholder="请再次输入密码"
				:error="!!$vuelidation.error('repeat_password')" />
		</van-cell-group>

		<div class="bottom_btn">
			<van-button size="large" type="danger" @click="modifypassword">保存</van-button>
		</div>
	</div>
</template>


<script>
  import { Picker, Toast } from 'vant';
	import { USER_UPDATE_PASSWORD, USER_LOGOUT } from '@/api/user';
	export default {
		data: () => ({
			password: "",
			new_password: "",
			repeat_password: ""
		}),

		vuelidation: {
			data: {
				password: {
					required: true,
					msg(){
						return "请输入密码"
					}
				},
				new_password: {
					alpha: true,
					required: true,
					betweenLength: {
						min: 6,
						max: 12,
					},
					msg(){
						return '请输入正确密码格式, 密码必须大于6位, 小于12位, 且只能包含数字和字母'
					}
				},
				repeat_password: {
					alpha: true,
					required: true,
					betweenLength: {
						min: 6,
						max: 12,
					},
					msg(){
						return '请输入正确密码格式, 密码必须大于6位, 小于12位, 且只能包含数字和字母'
					}
				}
			},
		},

		methods: {
			modifypassword(){
				if(this.mixValid() && this.passwordValid()){
					this.$reqPut(USER_UPDATE_PASSWORD, {
						old_password: this.password,
						password: this.new_password,
            re_password: this.repeat_password
					}).then(res => {
            let result = res.data;
            if(result.code!=0){
              Toast.fail(result.msg);
              return;
            }
						 return this.$dialog.alert({message: '保存成功, 请重新登录.'})
					}).then(() => {
						return this.$reqGet(USER_LOGOUT)
					}).then(()=>{
            this.$util.removeLocalStorage("portrait",
              "name",
              "address",
              "sex",
              "age",
              "bank",
              "qq",
              "wechat",
              "account",
              "card_no",
              "provide",
              "city",
              "county",
              "area_code");
            this.$router.push({name: 'login'});
					})
				}
			},
			passwordValid(){
				if(this.new_password != this.repeat_password){
					this.$toast("密码不一致, 请再次确认密码");
					return false;
				}
				return true;
      },
      //返回
      goBack() {
        this.$router.push({path: '/user'});
      }
		},
    components: {
      [Picker.name]: Picker,
      [Toast.name]: Toast
    }
	}

</script>

<style scoped>
	.bottom_btn{
		padding: 30px 15px 0 15px;
	}
</style>



